require('AIRobot');
require('QrOption');

// 斗地主的日志全局函数
ddz.LOGD = function(tag, msg){
	if (!debugMode){
		return;
	}
	var logStr = 'ddz : ' + msg;
	if (tag){
		logStr = tag + 'ddz : ' + msg;
	}
	console.log(logStr);
};

ddz.LOGW = function (tag, msg) {
	if (!debugMode){
		return;
	}
	var logStr = 'ddz : ' + msg;
	if (tag){
		logStr = tag + 'ddz : ' + msg;
	}
	console.warn(logStr);
};

